@use '../util' as *;

.cards {
    display: grid;
    grid-template-columns: 1fr;
    gap: rem(30);

    @include breakpoint(medium) {
        grid-template-columns: repeat(2, 1fr);
    }

    @include breakpoint(large) {
        grid-template-columns: repeat(4, 1fr);
    }
}

.card {
    position: relative;
    overflow: hidden;
    background: var(--dark-card);
    color: var(--dark-text1);
    padding: rem(25);
    border-radius: rem(5);
    text-align: center;
    transition: background 150ms ease-in-out;
    cursor: pointer;
    &:hover{
        background: var(--dark-card-hover);
    }
    &--facebook {
        border-top: rem(5) solid var(--facebook);
    }

    &--twitter {
        border-top: rem(5) solid var(--twitter);
    }

    &--instagram {
        padding-top: calc(rem(30));

        &::before {
            content: '';
            position: absolute;
            display: block;
            left: 0;
            top: 0;
            width: 100%;
            height: rem(5);
            background: linear-gradient(225deg, var(--instagram-start) 0%, var(--instagram-middle) 50.91%, var(--instagram-end) 100%);
        }
    }

    &--youtube {
        border-top: rem(5) solid var(--youtube);
    }

    &__username {
        font-size: rem(12);
        font-weight: bold;
    }

    &--grid{

    }

    &__platform {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: rem(5);
        margin-bottom: rem(28);
        height: rem(20);
    }

    &__subtitle{
        font-size: rem(14);
        font-weight: 700;
    }

    &__icon {
        margin-right: rem(8);
    }

    &__followers {
        margin-bottom: rem(25);
    }

    &__count {
        color: var(--light-bg);
        font-weight: 700;
        letter-spacing: rem(-2);
        line-height: 1;
        margin-bottom: rem(4);

        &--big {
            font-size: rem(56);
        }

        &--small {
            font-size: rem(32);
        }
    }

    &__label {
        color: var(--dark-text1);
        font-size: rem(12);
        font-weight: 400;
        text-transform: uppercase;
        letter-spacing: rem(5);
    }

    &__change {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: rem(12);
        font-weight: 700;

        &--up {
            color: var(--limegreen);
        }

        &--down {
            color: var(--brightred);
        }

        img {
            margin-right: rem(4);
        }
    }


}